<template>
    <div class="DecorCard">
      <div class="PicPanel">
        <img class="BigImg" src="./assets/uriy.svg"/>
        <div class="smallPanel">
          <img class="SmallImg" src="./assets/uriy.svg"/>
          <img class="SmallImg" src="./assets/uriy.svg"/>
          <img class="SmallImg" src="./assets/uriy.svg"/>
          <img class="SmallImg" src="./assets/uriy.svg"/>
        </div>

      </div>
      <div class="RightPanel">
        <div class="TopPanel">
          <div class="DecorName">{{decorName}}</div>
          {{params}}
        </div>
        <div class="Btns">
          <div class="btn status">Заказов нет</div>
          <div class="btn">Редактировать</div>
          <div class="btn">{{postBtn}}</div>
          <div class="btn">Удалить</div>
        </div>
        <div class="BotPanel">
          {{params}}
        </div>
      </div>


    </div>
</template>

<script>
    import BtnBar from "./BtnBar";
    export default {
        name: "DecorCard",
        components: {BtnBar},
        props: ["decorName", "params"],
          data: function () {
              return {
                posted: true
              }
          },
        computed:{
          postBtn: function () {
            if (this.posted) return 'Убрать с сайта'
            else return 'Добавить на сайт'
          }
        }
    }
</script>

<style lang="sass" scoped>
  $lgray: #f6f6f6
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .DecorCard
    background-color: $lgray
    //border: solid  1px
    box-shadow: $shadow
    width: 847px
    margin-bottom: 20px
    .PicPanel
      //background-color: lightgreen
      margin: 9px 0 0 11px
      display: inline-block
      img
        background-color: lightgrey
        //border: solid  1px
      .BigImg
        width: 186px
        height: 186px
        margin: 0
        padding: 0
      .smallPanel
        height: 40px
        margin: 0
        padding: 0
        .SmallImg
          height: 40px
          width:  40px
          display: inline
          margin-top: 4px
          margin-right: 4px
    .RightPanel
      display: inline-block
      vertical-align: top
      width: 630px
      .TopPanel
        //background-color: aliceblue
        display: inline-block
        min-height: 200px
        .DecorName
          font-size: 24px
          //font-weight: bold
          margin-top: 10px
          //margin: 10px
      .BotPanel
        //background-color: lightgoldenrodyellow
      .Btns
        float: right
        margin-top: 22px
        .btn
          display: block
          //background-color: white
          border: solid lightgrey 1px
          padding: 10px 0px 10px 0px
          margin-bottom: 10px
          text-align: center
          box-shadow: $shadow
          width: 171px
        .btn.status
          color: black
          box-shadow: none
  .min.DecorCard
    //background-color: lightskyblue
    //padding: 5px
    width: fit-content
    //text-align: center
    box-shadow: none
    display: inline-block
    margin-right: 10px
    .PicPanel
      display: block
      margin: 0
      .BigImg
        box-shadow: $shadow
      .smallPanel
        display: none
    .RightPanel
      display: block
      width: inherit
      .TopPanel
        //display: block
        min-height: fit-content
        text-align: left
        width: inherit
        .DecorName
          font-size: 18px
          font-weight: normal
          margin: 0px
      .Btns
        display: none
      .BotPanel
        display: none

</style>



