<template>
    <div :class="['DecorBody']">
      <div class="btn" @click="detalized=!detalized">{{btnName}}</div>
      <h1>Вазы</h1>
      <h2>На ножке</h2>
      <decor-card v-for="card in cards" :key="card.id" :decor-name="card.name" :params="card.params" :class="[detalized ? '' : 'min']"></decor-card>
    </div>
</template>

<script>
    import DecorCard from "./DecorCard";
    export default {
        name: "DecorBody",
        components: {DecorCard},
        data: function () {
              return {
                cards:[{'name':'My Vaza', 'params': {'param1':'val1'}},{'name':'My Vaza', 'params': {'param2':'val2'}}],
                detalized: 0,

            }
        },
        props:[],
        computed:{
          btnName: function () {
            if (this.detalized)return'Свернуть';
            else return'Развернуть';
          }
        },
        methods:{
        }
    }
</script>

<style lang="sass" scoped>
    $lgray: #f6f6f6
    $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
    .DecorBody
      //background-color: lightseagreen
      //padding-left: 20px
      .btn
        display: inline-block
        float: right
        background-color: white
        //border: solid black 1px
        padding: 10px 0 10px 0
        height: 17px
        width: 143px
        font-size: 15.5px
        text-align: center
        margin-top: -20px
        margin-right: 48px
        box-shadow: $shadow
</style>



