<template>
    <div class="LoginBlock" v-if="shown">
      <div class="switch btn" @click="guest=1-guest">
        <span v-if="guest">Войти</span>
        <span v-else>Зарегистрироваться</span>
      </div>
      <div class="content">

        <div class="warning">{{warning}}</div>
        <div class="line">
          <div class="label">Логин</div>
          <input/>
        </div>
        <div class="line">
          <div class="label">Пароль</div>
          <input/>
        </div>
        <div v-if="guest">
          <div class="line">
            <div class="label">Пароль еще раз</div>
            <input/>
          </div>
          <div class="line">
            <div class="label">Город</div>
            <input/>
          </div>
          <div class="line">
            <div class="label">Почта</div>
            <input/>
          </div>
          <div class="captcha">
            <img src='./assets/uriy.svg'/>
            <input/>
          </div>
          <div class="check">
            <input type="checkbox" v-model="shop"/>
            <div class="label">Хочу сдавать декор в аренду</div>
          </div>
          <div v-if="shop">
            <div class="line">
              <div class="label">Название компании</div>
              <input/>
            </div>
            <div class="line">
              <div class="label">Сайт</div>
              <input/>
            </div>
            <div class="line">
              <div class="label">Телефон</div>
              <input/>
            </div>
            <div class="line">
              <div class="label">Условия аренды</div>
              <input/>
            </div>
          </div>
        </div>
        <div v-else>
          <div class="remind" @click="sendNew=1-sendNew">Напомнить пароль</div>
          <div class="sendNewBlock" v-if="sendNew">
            <div class="line">
              <div class="label">Телефон/Почта</div>
              <input/>
              <div class="btn" >Отправить</div>
            </div>
          </div>
        </div>

        <div class="btns">
          <div class="btn" @click="$emit('hideLoginBlock');">Отмена</div>
          <div class="btn" v-if="guest" @click="">Подтвердить</div>
          <div class="btn" v-else @click="">Войти</div>
        </div>
      </div>


    </div>
</template>

<script>
    export default {
        name: "LoginBlock",
        props: ['shown'],
        data: function () {
            return {
              warning: 'Пароли не совпадают/ Код введен неверно',
              guest: 0,
              sendNew: false,
              shop: false,
            }
        }
    }
</script>

<style lang="sass" scoped>
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .LoginBlock
    background-color: white
    position: fixed
    box-shadow: $shadow
    margin-left: -700px
    //margin-top: 100px
    text-align: right
    font-size: 18px
    .switch
      margin: 20px
    .content
      padding: 30px 150px 50px 70px
      width: 550px
      .warning
        color: darkred
        height: 20px
      .line
        margin-top: 20px
        .label
          display: inline-block
        input
          width: 350px
          height: 37px
          margin-left: 30px
      .captcha
        margin-top: 30px
        img
          height: 55px
          width: 110px
          background-color: darkgray
          vertical-align: top
        input
          margin-left: 20px
          height: 50px
          font-size: 30px
          width: 250px
      .check
        margin-top: 20px
        margin-right: 115px
        input

        .label
          display: inline-block
      .remind
        color: darkgray
        margin: 10px
        cursor: pointer
      .btns
        margin-top: 20px
      .btn
        text-align: center
        display: inline-block
        margin-left: 80px
        padding: 8px 5px 8px 10px
        box-shadow: $shadow
        width: 150px
      .sendNewBlock
        .btn
          width: 250px
          margin: 25px

</style>



