<template>
    <div class="ColorPicker" v-if="shown">
        <div class="content">
          <div class="line">
            <div class="bar1 bar" v-for="b in colorData['1']"></div>
          </div>
          <div class="line">
            <div class="bar1 bar" v-for="b in colorData['2']"></div>
          </div>
          <div class="line">
            <div class="bar2 bar" v-for="b in colorData['3']"></div>
          </div>
          <div class="line">
            <div class="bar3 bar" v-for="b in colorData['4']"></div>
          </div>
          <div class="line">
            <div class="bar3 bar" v-for="b in colorData['5']"></div>
          </div>
          <div class="btns">
            <div class="btn" @click="$emit('hideColorPicker')">Отмена</div>
            <div class="btn" >Подтвердить</div>
          </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ColorPicker",
        props:['shown'],
        data: function () {
            return {
              colorData: {'1':'11111111111111111111','2':'11111111111111111111','3':'22222','4':'1111111111','5':'1111111111',}
            }
        }
    }
</script>

<style lang="sass" scoped>
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .ColorPicker
    background-color: white
    //border: solid lightgray 1px
    box-shadow: $shadow
    height: 710px
    width: 1010px
    position: absolute
    margin: -5px 0px 0px 40px
    .content
      margin-top: 23px
      margin-left: 35px

      .line
        margin-bottom: 18px
        .bar
          margin-right: 10px
          background-color: lightgray
          display: inline-block
          box-shadow: $shadow
        .bar1
          height: 112px
          width: 38px
        .bar2
          height: 38px
          width: 182px
        .bar3
          height: 112px
          width: 86px
      .btns
        text-align: center
        margin-top: 40px
        .btn
          text-align: center
          display: inline-block
          margin-right: 45px
          padding-top: 7px
          font-family: Philosopher
          font-size: 18px
          box-shadow: $shadow
          height: 30px
          width: 200px



</style>



